@use './utils' as *;

@mixin circle($size) {
  position: absolute;

  box-sizing: border-box;
  padding: $size;
  border-radius: 50%;
}

@mixin logoDescTop($size) {
  height: $size;
  width: $size;
}

@mixin  squere($size) {
  width: $size;
  height: $size;
}

@mixin squereCircle($size) {
  width: $size;
  height: $size;

  border-radius: 50%;
}
