@use '../../mixins.scss' as *;
@use '../../utils.scss' as *;

$elipseOne: 50px;
$elipseOneMobile: 20px;
$elipseTwo: 40px;
$elipseFive: 450px;

.Elipse-one {
  @include circle($elipseOne);
  top: 15vh;
  left: -5%;

  filter: blur(9px);

  background: linear-gradient($backGroundMain 33%, rgba(0, 83, 142, 0.47) 90%);
  opacity: 0.8;
  transform: rotate(125deg);

  z-index: 1;

  @media (max-width: 700px) {
    position: relative;
  }

  @media (max-width: 450px) {
    top: 57vh;
    left: -7%;
    transform: rotate(45deg);

    @include circle($elipseOneMobile);
  }

  @media (max-width: 330px) {
    top: 65vh;
    left: -12%;
  }

  &--little {
    @include squereCircle($elipseOne * 4);

    background-color: $backGroundMain;

    @media (max-width: 450px) {
      @include squereCircle($elipseOneMobile * 4);
    }
  }
}

.Elipse-two {
  @include circle($elipseOne);
  top: 55vh;
  left: 150%;

  filter: blur(9px);

  background: linear-gradient($backGroundMain 33%, rgba(0, 83, 142, 0.47) 140%);
  opacity: 0.8;
  transform: rotate(15deg);

  z-index: 1;

  @media (max-width: 700px) {
    position: relative;
  }

  @media (max-width: 450px) {
    top: 45vh;
    left: 105%;
    transform: rotate(20deg);

    @include circle(30px);

    background: linear-gradient($backGroundMain 33%, rgba(0, 83, 142, 0.47) 120%);
    filter: blur(5px);
  }

  &--little {
    @include squereCircle($elipseOne * 4);
    // width: 200px;
    // height: 200px;
    border-radius: 50%;

    background-color: $backGroundMain;


    @media (max-width: 450px) {
      @include squereCircle(30px * 4);
    }
  }
}

.Elipse-three {
  @include circle($elipseOne);
  top: 213vh;
  left: 110%;

  filter: blur(6px);

  background: linear-gradient($backGroundMain 33%, rgba(0, 83, 142, 0.47) 140%);
  opacity: 0.8;
  transform: rotate(15deg);

  z-index: 1;

  @media (max-width: 700px) {
    position: relative;
  }

  @media (max-width: 450px) {
    @include circle(25px);

    top: 330vh;
    left: 10vw;
    transform: rotate(80deg);
  }

  @media (max-width: 376px) {
    top: 297vh;
  }

  &--little {
    @include squereCircle($elipseOne * 4);
    border-radius: 50%;

    background-color: $backGroundMain;

    @media (max-width: 450px) {
      @include squereCircle(25px * 4);
    }
  }
}

.Elipse-four {
  @include circle($elipseTwo);
  top: 272vh;
  left: 140%;

  filter: blur(9px);

  background: linear-gradient($backGroundMain 33%, rgba(0, 83, 142, 0.47) 140%);
  opacity: 0.8;
  transform: rotate(-10deg);

  z-index: 1;

  @media (max-width: 700px) {
    position: relative;
  }

  @media (max-width: 450px) {
    @include circle(35px);

    top: 325vh;
    left: 58vw;
    transform: rotate(40deg);
  }

  @media (max-width: 376px) {
    top: 291vh;
  }

  &--little {
    @include squere($elipseTwo * 4);

    border-radius: 50%;

    background-color: $backGroundMain;

    @media (max-width: 450px) {
      @include squereCircle(31px * 4);
    }
  }
}

.Elipse-five {
  @include circle(60px);
  top: 415vh;
  left: 80%;

  filter: blur(9px);

  background: linear-gradient($backGroundMain 33%, rgba(0, 83, 142, 0.47) 100%);
  opacity: 0.8;
  transform: rotate(35deg);

  z-index: 1;

  &--little {
    @include squereCircle(65px *4);
    // width: 350px;
    // height: 350px;
    border-radius: 50%;

    background-color: $backGroundMain;
  }

  @media (max-width: 450px) {
    display: none;
  }
}

.line {
  position: absolute;
  top: 700vh;
  width: 70%;
  height: 800px;

  background-image: url('../../images/line.svg');
  background-repeat: no-repeat;
  background-size: cover;
  background-position: 0 -170px;

  @media (max-width: 450px) {
    display: none;
  }
}

.Elipse-six {
  @include circle(10px);
  padding: 20px;
  top: 767vh;
  left: 115%;

  filter: blur(9px);

  background: linear-gradient($backGroundMain 33%, rgba(0, 83, 142, 0.47) 100%);
  opacity: 0.8;
  transform: rotate(35deg);

  z-index: 1;

  &--little {
    // @include squere($elipseFive - 20px);
    width: 70px;
    height: 70px;
    border-radius: 50%;

    background-color: $backGroundMain;
  }

  @media (max-width: 450px) {
    display: none;
  }
}