.fourth-page {
  height: 110vh;
  z-index: 10;
  position: sticky;

  overflow: hidden;

  &__anim-cards {
    display: flex;
    flex-direction: column;

    @media (max-width: 913px) {
      margin-left: 10px;
    }

    @media (max-width: 450px) {
      flex-direction: row;
    }
  }

  // &__anim-body-mobile{
  //   display: flex;
  //   flex-direction: row;
  // }

  &__anim-body {
    display: flex;
    flex-direction: row;

    @media (max-width: 913px) {
      gap: 0px;
      padding: 0;
      margin: 10px;
    }

    @media (max-width: 450px) {
      flex-direction: column;
    }

    @media (max-width: 376px) {
      margin: 5px;
    }
  }

  &__anim-block {
    @include squere(300px);
    border: 1px solid $backcolorFirst;
    border-radius: 12px;
    margin: 0 0 22px 80px;

    animation: partners-translate-down infinite 80s linear;

    @media (max-width: 913px) {
      @include squere(100px);
      margin: 0 0 10px 10px;

      animation: partners-translate-down-1 infinite 25s linear;
    }

    @media (max-width: 450px) {
      margin-top: -620px;

      animation: partners-translate-right infinite 30s linear;
    }

    @media (max-width: 376px) {
      @include squere(115px);
      margin: -670px 0 2px 2px;

      animation: partners-translate-right infinite 30s linear;
    }

    // @media (max-width: 330px) {
    //   margin-top: -470px;
    // }

    &--2 {
      @include squere(300px);
      border: 1px solid $backcolorFirst;
      border-radius: 12px;
      margin: 0 0 22px 22px;

      transform: translateY(-3150px);

      animation: partners-translate-up infinite 80s linear;

      @media (max-width: 913px) {
        @include squere(100px);
        margin: 0 0 10px 10px;

        transform: translateY(-1200px);
        animation: partners-translate-up-1 infinite 80s linear;
      }

      @media (max-width: 450px) {
        margin-top: -250px;
        animation: none;

        animation: partners-translate-left infinite 40s linear;
      }

      @media (max-width: 376px) {
        @include squere(115px);
        margin: 5px 0 2px 2px;
      }
    }
  }

  &__picture {
    @include squere(100px);
    padding: 85px 0 0 0;

    @media (max-width: 913px) {
      @include squere(50px);
      padding: 25px 0 0 0;
    }

    @media (max-width: 450px) {
      @include squere(50px);
    }

    @media (max-width: 376px) {
      @include squere(60px);
    }
  }

  @keyframes partners-translate-down {
    0% {
      transform: translateY(-750px);
    }

    50% {
      transform: translateY(-3200px);
    }

    100% {
      transform: translateY(-750px);
    }
  }

  @keyframes partners-translate-down-1 {
    0% {
      transform: translateY(-800px);
    }

    50% {
      transform: translateY(-1100px);
    }

    100% {
      transform: translateY(-800px);
    }
  }

  @keyframes partners-translate-up {
    0% {
      transform: translateY(-3150px);
    }

    50% {
      transform: translateY(-750px);
    }

    100% {
      transform: translateY(-3150px);
    }
  }

  @keyframes partners-translate-up-1 {
    0% {
      transform: translateY(-1200px);
    }

    50% {
      transform: translateY(-700px);
    }

    100% {
      transform: translateY(-1200px);
    }
  }

  @keyframes partners-translate-right {
    0% {
      transform: translateX(-900px);
    }

    50% {
      transform: translateX(50px);
    }

    100% {
      transform: translateX(-900px);
    }
  }

  @keyframes partners-translate-left {
    0% {
      transform: translateX(20px);
    }

    50% {
      transform: translateX(-900px);
    }

    100% {
      transform: translateX(20px);
    }
  }

  &__h1 {
    margin: 0;
    padding: 0;
    width: 600px;

    position: absolute;
    top: 15%;
    left: 54.5%;

    text-align: left;
    font-size: 60px;
    color: $backcolorFirst;

    @media (max-width: 913px) {
      font-size: 30px;
      top: 320vh;
      left: 40vw;

      width: 320px;
    }

    @media (max-width: 650px) {
      font-size: 20px;
      top: 320vh;
      left: 50vw;
      width: 250px;
    }

    @media (max-width: 450px) {
      margin-top: 54px;

      width: 85%;
      font-size: 35px;
      top: 48vh;
      left: 2%;
    }

    @media (max-width: 376px) {
      margin-top: 54px;

      width: 85%;
      font-size: 25px;
      top: 48vh;
      left: 2%;
    }

    @media (max-width: 350px) {
      font-size: 22px;
    }

    @media (max-width: 330px) {
      font-size: 20px;
    }
  }

  &__text {
    position: absolute;
    // color: red;
    top: 60%;
    left: 55%;
    // top: 400vh;
    // left: 50vw;
    width: 480px;

    font-size: 25px;
    text-align: left;
    color: $backcolorFirst;

    @media (max-width: 913px) {
      width: 320px;

      top: 345vh;
      left: 40vw;

      font-size: 20px;
    }

    @media (max-width: 650px) {
      width: 250px;

      top: 345vh;
      left: 50vw;

      font-size: 20px;
    }

    @media (max-width: 450px) {
      width: 80%;
      margin-top: 40px;

      top: 78vh;
      left: 2%;

      font-size: 18px;
    }

    @media (max-width: 376px) {
      top: 71vh;
