.work {
  position: absolute;
  top: 740vh;
  left: 6vw;

  @media (max-width: 913px) {
    top: 680vh;
  }

  @media (max-width: 650px) {
    // top: 780vh;
  }

  @media (max-width: 450px) {
    top: 830vh;
  }

  @media (max-width: 415px) {
    top: 800vh;
  }

  @media (max-width: 413px) {
    top: 780vh;
  }

  @media (max-width: 401px) {
    top: 830vh;
  }

  @media (max-width: 394px) {
    top: 810vh;
  }

  @media (max-width: 391px) {
    top: 800vh;
  }


  @media (max-width: 376px) {
    top: 790vh;
  }

  @media (max-width: 330px) {
    top: 813vh;
  }

  @media (max-width: 301px) {
    top: 740vh;
  }

  &__position {
    width: 200px;
    height: 40px;
    overflow-y: hidden;

    @media (max-width: 450px) {
      width: 80vw;
    }
  }

  &__title {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    border-bottom: 1px solid $ColorText;
    padding-bottom: 2px;

    cursor: pointer;
  }

  &__description {
    height: 0px;
  }

  &__button {
    margin: 0;
    padding: 4px 7px 1px 7px;
    background-color: $ColorText;
    // border-color: $ColorText;
    border: none;
    border-radius: 50%;
    color: $backcolorFirst;

    cursor: pointer;
  }

  :hover {
    scale: 0.97;
  }
}

@keyframes jello {
  11.1% {
    transform: none
  }

  22.2% {
    transform: skewX(-8.5deg) skewY(-1.5deg) translateX(-30px);
  }

  33.3% {
    transform: skewX(4.25deg) skewY(4.25deg) translateX(10px);
  }

  44.4% {
    transform: skewX(-15.125deg) skewY(-5.125deg) translateX(-20px);
  }

  55.5% {
    transform: skewX(5.5625deg) skewY(5.5625deg) translateX(10px);
  }

  66.6% {
    transform: skewX(-4.78125deg) skewY(-4.78125deg) translateX(-10px);
  }

  77.7% {
    transform: skewX(3.390625deg) skewY(3.390625deg) translateX(10px);
  }

  88.8% {
    transform: skewX(-2.1953125deg) skewY(-2.1953125deg) translateX(-10px);
  }

  100% {
    transform: none
  }
}

.jello {
  animation: jello 3s infinite;
  transform-origin: center;

}

.center {
  display: inline-block;
}


.tab__open {
  @media (min-width: 450px) {
    display: none;
  }

  position: absolute;

  top: 740vh;
  left: 30%;

  color: $backcolorFirst;
  padding: 8px 50px;
  border: 1px solid $purpureColor;
  border-radius: 12px;

  background: linear-gradient(0.25turn, #00538E 0%, #9753A8 78%);
  cursor: pointer;

  font-family: 'Inter',
  sans-serif;
  font-size: 12px;

  @media (max-width: 415px) {
    top: 705vh;
  }

  @media (max-width: 413px) {
    top: 710vh;
  }

  @media (max-width: 401px) {
    top: 735vh;
  }

  @media (max-width: 399px) {
    top: 748vh;
  }

  @media (max-width: 394px) {
    top: 719vh;
  }
  @media (max-width: 389px) {
    top: 748vh;
