.Tab {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  text-align: center;
  align-items: center;
  gap: 5px;
  padding: 15px 0;
  box-sizing: border-box;

  width: 100%;
  height: 180%;

  position: absolute;
  top: 650vh;
  left: 0vw;
  z-index: 6;

  overflow-x: hidden;

  background-color: #00538E;
  border-radius: 12px;

  &__card {
    padding: 20px 20px;
    width: 90%;
    height: 800px;
    box-sizing: border-box;

    border: 1px solid $purpureColor;
    border-radius: 8px;

    color: $backGroundMain;
  }

  &__h1 {
    font-size: 15px;

    text-align: center;
    align-items: center;
  }

  &__text {
    font-size: 10px;

    padding: 10px 0;
  }

  &__close {
    color: $backcolorFirst;
    padding: 8px 50px;
    border: 1px solid $purpureColor;
    border-radius: 12px;

    background: linear-gradient(0.25turn, #00538E 0%, #9753A8 78%);
    cursor: pointer;

    font-family: 'Inter',
      sans-serif;
    font-size: 12px;
  }
}